form{
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

label{
    color: #1E1E1F;
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    margin-bottom: 26px;
}

textarea{
 background-color: #F9FBFC;
border: 2px solid #202329;
border-radius: 6px;
box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
color:#1E1E1F;
font-size: 20px;
text-align: center;
padding: 12px;
height: 96px;
width: calc(100% - 25px);
}
textarea::placeholder{
    color: #a5a5a5;
    font-family: 'Montserrat';
    font-weight: 400;
}

textarea:focus{
    outline-color: #61DAFA;
}

.TodoForm-buttonContainer{
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

}

.TodoForm-button{
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color:#202329;
    width: 120px;
    height: 48px;
    border-radius: 6px;
    border: none;
    font-family: 'Montserrat';


}
.TodoForm-button--add{
    background: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97    
    , 218, 250, 0.5);

}

.TodoForm-button--cancel{
    background: transparent;

}